.contact {
    min-height: 100vh;
    max-width: 100vw;
    color: white;
    margin-top: 20%;
}

.contact-title {
    height: 20%;
    padding-top: 15%;
    margin-left: 20%;
    color: hotpink;
    width: 60%;
    font-weight: bold;
    font-size: large;
}

.link-contact {
    width: 45%;
    height: 20%;
    float: left;
    margin-left: 28%;
    margin-top: 5%;
    margin-right: 10%;
    background-color:  #282c34;
}


.contact-info-end {
    width: 30%;
    background-color: #282c34;
    float: left;
    margin-left: 28%;
    margin-top: 3%;
    text-align: center;
}

.contact-info-item {
    width: 100%;
    margin-top: 6%;
    text-align: center;
    font-weight: bold;
}